import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

let router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        title: 'Главная'
      }
    },
    {
      path: '/tutorial',
      name: 'tutorial',
      component: () => import(/* webpackChunkName: "tutorial" */ './views/Tutorial'),
      meta: {
        title: 'Видео учебник'
      }
    }
  ]
})

export default router
