const namespaced = true

const state = {
  user: {
    id: 0,
    name: null,
    email: null,
    region_id: null,
    profession: null,
    phone: null,
    picture: null,
    balance: 0,
    statusId: 0,
    card: null
  }
}

const getters = {
  get: (state) => { return state.user }
}

const actions = {
  set ({ commit }, data) {
    commit('commitSet', data)
  }
}

const mutations = {
  commitSet (state, data) {
    state.user = Object.assign(state.user, data)
  }
}

export default {
  namespaced,
  state,
  mutations,
  actions,
  getters
}
